import React, { Component } from 'react'
import Navbar from './Navbar';
import HeaderContact from './HeaderContact';
import '../../styles/header.css'
import HeaderRoutes from './HeaderRoutes';
import MobileNav from './MobileNav';

export default class Header extends Component {

    state = {
      sideNav: {
        inView : false,
        display: 'none',
        transform: 'translateX(100%)',
        transition: 'transform 0.5s ease',
      },
      mobileNav: {
        inView : false,
        transform: 'translateX(-100%)',
        transition: 'transform 0.5s ease',
    }

    }

    handleSideNav = () => {
    const sideNav = {...this.state.sideNav};
    sideNav.inView ? sideNav.transform = 'translateX(100%)' : sideNav.transform = 'translateX(0%)' ;
    sideNav.inView = !sideNav.inView;
    this.setState({sideNav})
  }

  handleMobileNav = () => {
    const mobileNav = {...this.state.mobileNav};
    mobileNav.inView ? mobileNav.transform = 'translateX(-100%)' : mobileNav.transform = 'translateX(0%)' ;
    mobileNav.inView = !mobileNav.inView;
    this.setState({mobileNav})
  }

    render() {
        return (<React.Fragment>
           
            <header className='header-nav'>
                <MobileNav handleSideNav={this.handleSideNav}  mobileNav={this.state.mobileNav} handleMobileNav={this.handleMobileNav} />
                <HeaderContact />
                <Navbar handleSideNav={this.handleSideNav} sideNav={this.state.sideNav} handleMobileNav={this.handleMobileNav} />
                <HeaderRoutes />
            </header>
            </React.Fragment>
        )
    }
}
