import React, { Component } from 'react';
import {NavLink} from 'react-router-dom';
// import logo1 from '../img/logo1.png';
// import logo from '../img/logo.png';
import '../../styles/navbar.css';

export default class Navbar extends Component {

  state = {
    navStyle: {
      navBackground: "transparent",
      logo: require('../../img/logo.png'),
      borderTop: '0.5px solid #E5DADA',
      borderBottom: 'none',
      position: 'static',
    },
   
  };


   componentDidMount() {
     // setting nav styling on scroll
    document.addEventListener("scroll", () => {
      const backgroundcolor = window.scrollY < 1 ? "" : "white";
      const color = window.scrollY < 1 ? "white" : "black";
      const position = window.scrollY < 1 ? "static" : "fixed";
      const logo = window.scrollY < 1 ? require('../../img/logo.png') : require('../../img/logo1.png');
      const borderTop = window.scrollY < 1 ? '1px solid #e5dada' : 'none';
      const borderBottom = window.scrollY < 1 ?  'none' : '1px solid #e5dada';
      const navStyle = {...this.state.navStyle};
      navStyle.navBackground = backgroundcolor;
      navStyle.color = color;
      navStyle.logo = logo;
      navStyle.position = position;
      navStyle.borderTop = borderTop;
      navStyle.borderBottom = borderBottom;
      this.setState({ navStyle });
    });
  }



    render() {
      const {navStyle} = this.state;
      const {sideNav} = this.props
        return (
           <div style={{background:navStyle.navBackground, borderTop:navStyle.borderTop, borderBottom:navStyle.borderBottom, position: navStyle.position}} id='navbar' className='navbar'>
             
             <div onClick={this.props.handleMobileNav} className="mobile-hamburger">
               <div style={{backgroundColor: navStyle.color}} className="ham-bar"></div>
               <div style={{backgroundColor: navStyle.color}} className="ham-bar"></div>
               <div style={{backgroundColor: navStyle.color}} className="ham-bar"></div>
             </div>
             
             <div className="nav-section nav-logo-container">
               <img src={navStyle.logo} alt=""/>
             </div>
             <ul className="navbar-links nav-section">
               <li>
                 <NavLink style={{color:navStyle.color}} className='nav-link' to='/'>Home</NavLink>
               </li>
               <li>
                 <NavLink style={{color:navStyle.color}} className='nav-link' to='/about'>About Us</NavLink>
               </li>
               <li>
                 <NavLink style={{color:navStyle.color}} className='nav-link' to='/projects'>Projects</NavLink>
               </li>
               <li >
                 <NavLink style={{color:navStyle.color}} className='nav-link' to='/services'>Services</NavLink>
               </li>
               <li >
                 <NavLink style={{color:navStyle.color}} className='nav-link' to='/news'>News</NavLink>
               </li>
               <li >
                 <NavLink style={{color:navStyle.color}} className='nav-link' to='/contact'>Contact</NavLink>
               </li>
             </ul>

             <div  className="more-info nav-section">
               <i style={{color: navStyle.color}} className="fas fa-search"></i>
               <span className="quote">Request a Quote</span>
               <i onClick={this.props.handleSideNav} style={{color: navStyle.color}} className="fas fa-bars"></i>
             </div>
             <div style={{transform: sideNav.transform ,transition: sideNav.transition}} id='side-nav' className="side-nav">
               <div className="moreinfo-container"> <span className='sidenav-heading'>More info</span> 
                <span  onClick={this.props.handleSideNav} className='close-sidenav'><i className="fas fa-times"></i></span></div>
               <div className="sidenav-links">
                 <NavLink to='/' className='sidenav-link'>Request a quote</NavLink>
                 <NavLink to='/' className='sidenav-link'>Privacy Policy</NavLink>
                 <NavLink to='/' className='sidenav-link'>Cookie Policy</NavLink>
                 <NavLink to='/' className='sidenav-link'>Terms and Conditions</NavLink>
               </div>
             </div>
           </div>
        )
    }
}
