import React from 'react'
import img from '../../img/about-construction.jpg'
import hardwork from '../../img/progressbar-hardwork.png';
import delivery from '../../img/progressbar-delivery.png';
import customers from '../../img/progressbar-customers.png';

function AboutConstruction() {
    return (
        <div className='about-construction'>
            
            <div className=" construction-text">      
                <h2 className='construction-heading'>From idea to real <br/>
                    <span className="construction-strong">construction</span> 
                </h2>
                <p className="construction-paragraph">
                    Robert’s 21st Century Design is one of the UK’s leading contractor for residential and business customers since 2008. With hundreds of projects finished, Robert’s 21st Century Design will deliver a high quality service that will delight you at all times.
                </p>
            </div>
            
            <div className="progress-bars">
                <div className="progress-container">
                    <img src={hardwork} alt=""/>
                </div>
                <div className="progress-container">
                    <img src={delivery} alt=""/>
                </div>
                <div className="progress-container">
                    <img src={customers} alt=""/>
                </div>
            </div>              
        
            <div className="about-construction-img">
                <img src={img} alt=""/>
            </div>

            <div className="mobile-construction-img">
                <img src={img} alt=""/>
            </div>
            
        </div>
    )
}

export default AboutConstruction
