import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';
import '../../styles/mobilenav.css'
import logo1 from '../../img/logo1.png'
import logo from '../../img/logo.png'


export default class MobileNav extends Component {
    render() {
        const {mobileNav} = this.props;
        return (
            <div style={{transform: mobileNav.transform, transition: mobileNav.transition}} className='mobile-nav'>
                
                <div className="mobilenav-header">
                    <div className="logo-container">
                        <img src={logo1} alt=""/>
                    </div>
                    <div className="closenav"><i onClick={this.props.handleMobileNav} className="fas fa-times fa-lg"></i></div>
                </div>

                <div className="mobilenav-links">
                    <li className="mobilenav-route-link mobilenav-secondlogo">
                        <NavLink to='/'><img src={logo} alt=""/></NavLink>
                    </li>
                    <li className="mobilenav-link">
                        <NavLink className='mobilenav-route-link' to='/'>Home</NavLink>
                    </li>
                    <li className="mobilenav-link">
                        <NavLink className='mobilenav-route-link' to='/'>About Us</NavLink>
                    </li>
                    <li className="mobilenav-link">
                        <NavLink className='mobilenav-route-link' to='/'>Projects</NavLink>
                    </li>
                    <li className="mobilenav-link">
                        <NavLink className='mobilenav-route-link' to='/'>Services</NavLink>
                    </li>
                    <li className="mobilenav-link">
                        <NavLink className='mobilenav-route-link' to='/'>News</NavLink>
                    </li>
                    <li className="mobilenav-link">
                        <NavLink className='mobilenav-route-link' to='/'>Contact</NavLink>
                    </li>
                </div>

                <div className="mobilenav-moreinfo">
                    <div className="mobilenav-searchwebsite mobilenav-moreinfo-link">
                        <i className="fas fa-search"></i> <span className='mobilenav-moreinfo-text'>Search website</span>
                    </div>  
                    <div className="mobilenav-quote mobilenav-moreinfo-link">
                        <span>Request a Quote</span>
                    </div>
                    <div onClick={this.props.handleSideNav} className="mobilenav-sidemenu mobilenav-moreinfo-link">
                        <i className="fas fa-bars"></i> <span className='mobilenav-moreinfo-text'>Side Menu</span>
                    </div>
                    <div className="mobilenav-contact mobilenav-moreinfo-link">
                         <span className='mobilenav-moreinfo-text number'><i className="fas fa-phone"></i> 0800 756 6392</span>
                    </div>
                    <div className="mobilenav-contact mobilenav-moreinfo-link">
                        <span className='mobilenav-moreinfo-text email'><i className="fas fa-envelope-open"></i>Info@R21stcd.Com</span> 
                    </div>
                </div>
            </div>
        )
    }
}
